  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

body {
  font-family: var(--main-font);
  color: var(--main-txt-cl);
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.no-scroll {
