}

*, :before, :after {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6, p {
  letter-spacing: .03em;
  margin: 0;
}

ul, ol {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
  object-position: center;
  display: block;
}

button {
  cursor: pointer;
}

address {
  font-style: normal;
}

textarea {
  resize: none;
}

