.contacts__link, .header-contacts__link, .header__container, .site-nav__link, .site-nav {
  align-items: center;
  display: flex;
}

.feedback-form__btn, .portfolio__overlay, .clients__link, .modal-form__btn, .modal-form__agreement-label, .close__btn, .subscribe__btn, .social__link, .social__list {
