}

@media screen and (min-width: 768px) {
  .contacts-title, .clients-title, .team__title, .about__title {
    font-size: var(--fs-section-title-t);
    line-height: var(--lh-section-title-t);
  }
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.site-nav {
  color: var(--title-txt-cl);
}

@media screen and (max-width: 767px) {
  .site-nav__list {
    font-size: var(--fs-site-nav);
