}

.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (min-width: 480px) {
  .container {
    width: var(--mobile);
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: var(--tablet);
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: var(--desktop);
  }
}

.card-set {
  gap: var(--indent);
  flex-wrap: wrap;
  display: flex;
}

.card-item {
  flex-basis: calc((100% - var(--indent) * (var(--item)  - 1) ) / var(--item) );
}

.social__list {
