  background-color: var(--accent-cl);
}

.social__link--dark {
  color: var(--white-txt-cl);
  background-color: var(--background-dark-social-bg-cl);
}

.social__icons {
  fill: currentColor;
}

.subscribe__title {
  color: var(--white-txt-cl);
