  }
}

@media screen and (min-width: 768px) {
  .subscribe__input {
    width: 450px;
  }
}

@media screen and (min-width: 1200px) {
  .subscribe__input {
    width: 358px;
  }
}

.subscribe__input:hover, .subscribe__input:focus {
  border-color: var(--accent-cl);
}

.subscribe__input::-ms-input-placeholder {
  font-style: italic;
}

.subscribe__input::placeholder {
  font-style: italic;
}

.subscribe__btn {
  min-width: 200px;
  height: 50px;
  box-shadow: var(--subscribe-shadow);
}

@media screen and (max-width: 1199px) {
  .subscribe__btn {
    margin: 0 auto;
  }
}

.subscribe__icon {
  fill: #fff;
  margin-left: 10px;
}

.filter {
  padding-top: 60px;
  padding-bottom: 40px;
}

@media screen and (max-width: 767px) {
  .filter {
    margin-top: 64px;
  }
}

@media screen and (min-width: 768px) {
  .filter {
    margin-top: 81px;
    padding-top: 60px;
    padding-bottom: 30px;
  }
}

@media screen and (min-width: 1200px) {
  .filter {
    padding-top: 94px;
    padding-bottom: 60px;
  }
}

.filter__list {
  width: 326px;
  flex-wrap: wrap;
  justify-content: flex-start;
  gap: 10px;
  margin-bottom: 40px;
  display: flex;
}

@media screen and (max-width: 767px) {
  .filter__list {
    margin-bottom: 40px;
  }
}

@media screen and (min-width: 768px) {
  .filter__list {
    width: 100%;
    justify-content: center;
    margin-bottom: 30px;
  }
}

@media screen and (min-width: 1200px) {
  .filter__list {
    margin-bottom: 50px;
  }
}

.filter__btn {
  font-weight: var(--fw-filter-btn);
  font-size: var(--fs-filter-btn-s);
  line-height: var(--lh-filter-btn);
  letter-spacing: var(--ls-filter-btn);
  text-align: center;
  color: var(--title-txt-cl);
  background-color: var(--background-light-cl);
  border-radius: var(--border-radius);
  transition-property: color, background-color, box-shadow;
  transition-duration: var(--duration);
  transition-timing-function: var(--timing-function);
  padding: 6px 22px;
  display: block;
}

@media screen and (min-width: 480px) {
  .filter__btn {
    font-size: var(--fs-filter-btn-m);
  }
}

.filter__btn:hover, .filter__btn:focus, .filter__btn:checked + .filter__btn {
  color: var(--white-txt-cl);
  background-color: var(--accent-cl);
  box-shadow: var(--filter-btn-shadow);
}

@media screen and (max-width: 767px) {
  .burger__btn {
    width: 24px;
    height: 17px;
    display: block;
    position: relative;
  }

  .burger__btn:before, .burger__btn:after {
    content: "";
    width: 100%;
    height: 3px;
    background-color: var(--icon-menu-cl);
    position: absolute;
    left: 0;
  }

  .burger__btn:before {
    transition-property: transform;
    transition-duration: var(--duration);
    transition-timing-function: var(--timing-function);
    top: 0;
  }

  .burger__btn:after {
    transition-property: transform;
    transition-duration: var(--duration);
    transition-timing-function: var(--timing-function);
    bottom: 0;
  }
}

.burger__btn.active:before {
  top: 7px;
  transform: rotate(45deg);
}

.burger__btn.active:after {
  top: 7px;
  transform: rotate(-45deg);
}

.burger__btn.active .burger__icon {
  opacity: 0;
}

.burger__icon {
  width: 100%;
  height: 3px;
  background-color: var(--icon-menu-cl);
  position: absolute;
  top: 7px;
  left: 0;
}

@media screen and (max-width: 767px) {
  .mobile-menu {
    width: 100vw;
    height: 100vh;
    border-top: 1px solid var(--header-border-cl);
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transition-property: transform, opacity, visibility;
    transition-duration: var(--duration);
    transition-timing-function: var(--timing-function);
    background-color: #fff;
    position: absolute;
    top: 100%;
    left: 0;
    overflow-y: scroll;
    transform: translateY(-100%);
  }
}

@media screen and (min-width: 768px) {
  .mobile-menu {
    flex-grow: 1;
    justify-content: space-between;
    align-items: center;
    display: flex;
  }
}

.mobile-menu.is-open {
  opacity: 1;
  visibility: visible;
  pointer-events: initial;
  transform: translateY(0);
}

.mobile-menu.is-open .mobile-menu__container {
  border-bottom: none;
}

@media screen and (max-width: 767px) {
  .mobile-menu__container {
    max-width: 480px;
    height: 100%;
    border-bottom: 1px solid var(--header-border-cl);
    flex-direction: column;
    justify-content: space-between;
    margin: 0 auto;
    padding: 34px 40px 100px;
    display: flex;
  }
}

@media screen and (min-width: 768px) {
  .mobile-menu__container {
    flex-grow: 1;
    justify-content: space-between;
    align-items: center;
    display: flex;
  }
}

.backdrop {
  width: 100%;
  height: 100%;
  z-index: 400;
  background-color: var(--backdrop-cl);
  -webkit-backdrop-filter: blur(2px);
  backdrop-filter: blur(2px);
  transition-property: opacity, visibility;
  transition-duration: var(--duration);
  transition-timing-function: var(--timing-function);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal {
  transform: translate(-50%, -50%) scale(1) rotate(0);
  width: 290px;
  height: auto;
  background-color: var(--background-white-cl);
  border-radius: var(--border-radius);
  box-shadow: var(--modal-shadow);
  transition-property: transform, opacity;
  transition-duration: var(--duration);
  transition-timing-function: var(--timing-function);
  padding: 40px 10px;
  position: absolute;
  top: 50%;
  left: 50%;
}

@media screen and (min-width: 480px) {
  .modal {
    width: 450px;
    padding: 40px;
  }
}

@media screen and (min-width: 1200px) {
  .modal {
    width: 528px;
  }
}

.backdrop.is-hidden .modal {
  opacity: 0;
  transform: translate(-50%, -50%)scale(0)rotate(90deg);
}

.close__btn {
  width: 30px;
  height: 30px;
  border-width: 1px;
  border-color: var(--btn-border-cl);
  transition-property: fill;
  transition-duration: var(--duration);
  transition-timing-function: var(--timing-function);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  padding: 0;
  position: absolute;
  top: 8px;
  right: 8px;
}

.close__btn:hover, .close__btn:focus {
  fill: var(--accent-cl);
}

.modal__title {
  font-size: var(--fs-modal-title);
  line-height: var(--lh-modal-title);
  color: var(--title-txt-cl);
  text-align: center;
  margin-bottom: 12px;
  display: block;
}

.modal-form__agreement-label {
  cursor: pointer;
  gap: 8px;
  margin-bottom: 30px;
}

.modal-form__agreement-input {
  display: none;
}

.modal-form__agreement-icon {
  color: var(--icon-modal-cl);
  transition-property: color;
  transition-duration: var(--duration);
  transition-timing-function: var(--timing-function);
}

.modal-form__agreement-label:hover .modal-form__agreement-icon {
  color: var(--accent-cl);
}

.modal-form__agreement-unchecked {
  fill: currentColor;
}

.modal-form__agreement-checked {
  opacity: 0;
